import React, { useState } from "react";
import { Paper, Grid, Typography } from "@material-ui/core";
import { useStyles } from "../style/navigatorStyle";
import resumeFr from "../assets/resumeFR.pdf";

export default function Navigator() {
  const classes = useStyles();
  const [hovered, setHovered] = useState(0);
  const handleHover = (id) => setHovered(id);

  return (
    <Grid className={classes.top} container justify="center">
      <Grid
        item
        onMouseEnter={() => handleHover(1)}
        onMouseLeave={() => setHovered(0)}
      >
        <Paper
          elevation={3}
          className={hovered === 1 ? classes.hoverSpace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 1 ? classes.hoverText : classes.text}
          >
            Projects
          </Typography>
        </Paper>
      </Grid>
      <Grid
        item
        onMouseEnter={() => handleHover(2)}
        onMouseLeave={() => setHovered(0)}
      >
        <Paper
          elevation={3}
          className={hovered === 2 ? classes.hoverSpace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 2 ? classes.hoverText : classes.text}
          >
            <a href={resumeFr} className={classes.link} download>Resume</a>
          </Typography>
        </Paper>
      </Grid>
      <Grid
        item
        onMouseEnter={() => handleHover(3)}
        onMouseLeave={() => setHovered(0)}
      >
        <Paper
          elevation={3}
          className={hovered === 3 ? classes.hoverSpace : classes.space}
        >
          <Typography
            align="center"
            className={hovered === 3 ? classes.hoverText : classes.text}
          >
            <a
              href="mailto:&#106;&#111;&#104;&#97;&#110;&#100;&#97;&#118;&#105;&#100;&#64;&#109;&#101;&#46;&#99;&#111;&#109;"
              className={classes.link}
            >
              Contact
            </a>
          </Typography>
        </Paper>
      </Grid>
    </Grid>
  );
}
