import React, { useContext, useState } from "react";
import {
  ThemeProvider,
  createMuiTheme,
  Typography,
  Grid,
  Link,
} from "@material-ui/core";
import { Brightness4, Brightness7 } from "@material-ui/icons";
import Photographie from "../assets/photographie.jpg";
import Logo from "../assets/logo.jpg";
import { CustomThemeContext } from "../theme/theme";
import { useStyles } from "../style/headerStyle";

export default function Header() {
  const classes = useStyles();
  const [photo, setPhoto] = useState(Photographie);
  const { currentTheme, setTheme } = useContext(CustomThemeContext);

  const hoverPicture = () => {
    photo === Photographie ? setPhoto(Logo) : setPhoto(Photographie);
  };

  const themes = createMuiTheme({
    typography: {
      fontFamily: ["Quicksand"],
    },
  });

  const handleThemeChange = () => {
    currentTheme === "normal" ? setTheme("dark") : setTheme("normal");
  };

  return (
    <>
      <ThemeProvider theme={themes}>
        <Grid className={classes.top} container justify="center" spacing={2}>
          <Grid item>
            <div
              className={classes.photos}
              style={{ backgroundImage: `url(${photo})` }}
              onMouseEnter={hoverPicture}
              onMouseLeave={hoverPicture}
            />
          </Grid>
          <Grid item>
            <Grid container>
              <Grid item>
                <Grid container>
                  <Grid item>
                    <Typography className={classes.nom}>
                      Johan-David Dehi
                    </Typography>
                  </Grid>
                  <Grid
                    item
                    className={classes.logo}
                    onClick={handleThemeChange}
                  >
                    {currentTheme === "normal" ? (
                      <Brightness4
                        fontSize="large"
                        style={{ color: "#121212" }}
                      />
                    ) : (
                      <Brightness7
                        fontSize="large"
                        style={{ color: "#ECB538" }}
                      />
                    )}
                  </Grid>
                </Grid>
                <Typography className={classes.subName}>
                  Software warrior &#183; JavaScript lover
                </Typography>
              </Grid>
            </Grid>
            <Grid item>
              <Link
                href="https://github.com/Samorinho"
                target="_blank"
                className={classes.link}
              >
                <Typography className={classes.mobile}>Github</Typography>
              </Link>
            </Grid>
            <Grid item>
              <Link
                href="https://www.linkedin.com/in/monsieurdehi/"
                target="_blank"
                className={classes.link}
              >
                <Typography className={classes.mobile}>LinkedIn</Typography>
              </Link>
            </Grid>
            <Grid item>
              <Link
                href="https://instagram.com/monsieurdehi"
                target="_blank"
                className={classes.link}
              >
                <Typography className={classes.mobile}>Instagram</Typography>
              </Link>
            </Grid>
            <Grid item>
              <Link
                href="https://afropedia.fr"
                target="_blank"
                className={classes.link}
              >
                <Typography className={classes.mobile}>Afropedia</Typography>
              </Link>
            </Grid>
          </Grid>
        </Grid>
      </ThemeProvider>
    </>
  );
}
