import React, { useState } from "react";
import { Paper, Grid, Typography } from "@material-ui/core";
import { datalist } from "./data";
import { useStyles } from "../style/projectsStyle";

export default function Projects() {
  const classes = useStyles();
  const [selected, setSelected] = useState("");

  const handleVisible = (id) => setSelected(id);

  return (
    <Grid className={classes.top} container justify="center" direction="column">
      {datalist.map((data) => (
        <Grid item key={data.id}>
          <Paper
            elevation={3}
            className={classes.space}
            style={{
              backgroundImage: data.id === selected ? "" : `url(${data.logo})`,
              backgroundPosition: "5% 75%",
              backgroundSize: "20%",
              backgroundRepeat: "no-repeat",
              //   backgroundColor: "rgba(0,0,0,0.3)",
              // filter: "blur(8px)"
            }}
            onMouseEnter={() => handleVisible(data.id)}
            onMouseLeave={() => setSelected("")}
          >
            {data.id === selected ? (
              <>
                <Typography align="justify" className={classes.read}>
                  {data.description}
                </Typography>
                <Typography align="justify" className={classes.read}>
                  Stack : {data.techno}
                </Typography>
              </>
            ) : (
              <>
                <Typography align="center" className={classes.text}>
                  {data.name}
                </Typography>
                <Grid
                  container
                  spacing={3}
                  className={classes.pad}
                  justify="flex-end"
                >
                  <Grid item>
                    <Typography color="secondary">{data.type}</Typography>
                  </Grid>
                  <Grid item>
                    <Typography color="secondary">{data.date}</Typography>
                  </Grid>
                </Grid>
              </>
            )}
          </Paper>
        </Grid>
      ))}
    </Grid>
  );
}
